body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.search-wrapper {
	--button-width: 100px;
	flex: 1 1;
	max-width: 500px;
	position: relative;
}

.search-form {
	display: flex;
}

.search-form__input {
	font-size: 18px;
	padding: 10px;
	border: 2px solid #efefef;
	border-radius: var(--border-radius);
	max-width: 500px;
	flex-basis: 400px;
	width: 100%;
}

.search-form__input:focus {
	border-color: var(--brand-color);
}

.search-form__input::-webkit-input-placeholder {
	color: #bbbbbb;
	opacity: 1;
}

.search-form__input::-moz-placeholder {
	color: #bbbbbb;
	opacity: 1;
}

.search-form__input::-ms-input-placeholder {
	color: #bbbbbb;
	opacity: 1;
}

.search-form__input::placeholder {
	color: #bbbbbb;
	opacity: 1;
}

.search-form__button {
	width: var(--button-width);
	flex: 0 1;
	margin-left: 8px;
	font-size: 18px;
	padding: 10px 15px;
	border: 2px solid var(--brand-color);
	border-radius: var(--border-radius);
	-webkit-transition: background-color var(--transition-duration) ease-in;
	transition: background-color var(--transition-duration) ease-in;
}

.search-form__button:hover,
.search-form__button:focus {
	background-color: var(--brand-color);
}

.live-results {
	position: absolute;
	top: calc(100% + 4px);
	border-radius: var(--border-radius);
	left: 0;
	width: calc(100% - var(--button-width));
	border: 2px solid #efefef;
	background-color: #fff;
	margin: 0;
	padding-left: 0;
	list-style: none;
}

.live-results--empty {
	padding: 10px;
}

.live-results__item {
	position: relative;
	display: flex;
	align-items: center;
	padding: 10px;
	-webkit-transition: background-color var(--transition-duration) ease-in;
	transition: background-color var(--transition-duration) ease-in;
	font-size: 18px;
}

.live-results__item:hover,
.live-results__item:focus-within {
	background-color: #efefef;
}

.live-results__image {
	max-width: 40px;
	border-radius: var(--border-radius);
	margin-right: 8px;
}

.live-results__link {
	text-decoration: none;
	color: inherit;
}

.live-results__link::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.live-results__year {
	color: #ccc;
	font-size: 12px;
}

.live-results__rating {
	display: inline-block;
	margin-top: 4px;
}
.header {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: var(--gutter);
	background-color: #fff;
	z-index: 2;
	border-bottom: 2px solid #efefef;
}

.nav-list {
	display: flex;
	padding-left: 0;
	list-style: none;
	margin: 0;
	color: #bbbbbb;
}

.nav-list__item {
	position: relative;
	margin: 10px;
}

.nav-list__item--current {
	color: #000;
}

.nav-list__link {
	position: relative;
	display: inline;
	padding: 5px;
	color: inherit;
	text-decoration: none;
	-webkit-transition: color var(--transition-duration) ease-in;
	transition: color var(--transition-duration) ease-in;
}

.nav-list__link::before {
	content: "";
	width: 100%;
	position: absolute;
	left: 0;
	bottom: 0;
	height: 2px;
	background-color: var(--brand-color);
	opacity: 0;
	-webkit-transition: opacity var(--transition-duration) ease-in;
	transition: opacity var(--transition-duration) ease-in;
}

.nav-list__link:hover,
.nav-list__link:focus {
	color: #000;
}

.nav-list__item--current .nav-list__link::before,
.nav-list__item--current .nav-list__link::before {
	opacity: 1;
}
.page-title {
	font-size: 28px;
	position: relative;
	display: inline-block;
	margin-bottom: 20px;
	margin-top: 0;
}

.page-title::before {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	height: 15px;
	background-color: var(--brand-color);
	z-index: -1;
	bottom: 0px;
}

.page-content {
	padding-top: 30px;
}
.favorite-button {
	padding: 5px;
	background: none;
	border: 0;
}

.favorite-icon {
	width: 25px;
	height: 25px;
}
.favorite-icon path {
	fill: transparent;
	stroke:#fff;
	stroke-width: 2px;
	-webkit-transition: stroke var(--transition-duration) ease-in, fill var(--transition-duration) ease-in;
	transition: stroke var(--transition-duration) ease-in, fill var(--transition-duration) ease-in
}

.favorite-icon--active path {
	stroke: #efce4a;
	fill: #efce4a
}
.film-item {
	position: relative;
	padding-top: 150%;
	overflow: hidden;
	box-shadow: 1px 1px 6px 0 rgba(0,0,0,.4);
  border-radius: var(--border-radius);
}

.film-item__image-wrapper {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.film-item__image-wrapper::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #000;
	opacity: 0;
	-webkit-transition: opacity var(--transition-duration) ease-in;
	transition: opacity var(--transition-duration) ease-in; 
}

.film-item:hover .film-item__image-wrapper::before,
.film-item:focus-within .film-item__image-wrapper::before {
	opacity: 0.5;
}

.film-item__image {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.film-item__inner {
	position: absolute;
	top: var(--film-card-vertical-offset);
	right: var(--film-card-horizontal-offset);
	bottom: var(--film-card-vertical-offset);
	left: var(--film-card-horizontal-offset);
	display: flex;
	flex-direction: column-reverse;
	justify-content: space-between;
}

.film-item__title {
	font-size: 24px;
	color: #fff;
	margin: 0;
	opacity: 0;
	-webkit-transition: opacity var(--transition-duration) ease-in;
	transition: opacity var(--transition-duration) ease-in;
}

.film-item:hover .film-item__title,
.film-item:focus-within .film-item__title{
	opacity: 1;
}

.film-item__link {
	color: inherit;
	text-decoration: none;
}

.film-item__link::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.film-item__top {
	display: flex;
	justify-content: space-between;
	align-items: center;
	-webkit-transition: -webkit-transform var(--transition-duration) ease-in;
	transition: -webkit-transform var(--transition-duration) ease-in;
	transition: transform var(--transition-duration) ease-in;
	transition: transform var(--transition-duration) ease-in, -webkit-transform var(--transition-duration) ease-in;
	-webkit-transform: translateY(calc(-100% - var(--film-card-vertical-offset)));
	        transform: translateY(calc(-100% - var(--film-card-vertical-offset)));
}

.film-item:hover .film-item__top,
.film-item:focus-within .film-item__top{
	-webkit-transform: translateY(0);
	        transform: translateY(0)
}

.rating {
	background-color: #efce4a;
	color: #000;
	border-radius: var(--border-radius);
	padding: 2px 5px;
	font-size: 10px;
}

.films-list {
  --count-item-perline: 1;
  --count-on-large-mobile: 2;
  --count-on-tablet: 4;
  --count-on-desctop: 6;

  padding-left: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin-top: 0;
  margin-bottom: 20px;
}

@media (min-width: 480px) {
  .films-list {
    --count-item-perline: 2;
  }
}

@media (min-width: 768px) {
  .films-list {
    --count-item-perline: 3;
  }
}

@media (min-width: 992px) {
  .films-list {
    --count-item-perline: 4;
  }
}

@media (min-width: 1200px) {
  .films-list {
    --count-item-perline: 5;
  }
}

.films-list__item {
  width: calc(100vw / var(--count-item-perline));
  padding-left: var(--gutter);
  padding-right: var(--gutter);
  margin-bottom: calc(var(--gutter) * 2);
}

@media (min-width: 1500px) {
  .films-list__item {
    flex: 1 1;
    width: auto;
    min-width: 250px;
    max-width: 300px;
  }
}

.pagination {
	display: flex;
	padding-left: 0;
	list-style: none;
	justify-content: center;
}

.pagination__item {
	color: #000;
	border: 2px solid var(--brand-color);
	border-radius: var(--border-radius);
	margin-left: 6px;
	margin: 8px;
	font-size: 18px;
}

.pagination__item--current .pagination__link {
	pointer-events: none;
	background-color: var(--brand-color);
}

.pagination__link {
	display: inline-block;
	min-width: 45px;
	text-align: center;
	color: inherit;
	text-decoration: none;
	padding: 12px;
	-webkit-transition: background-color var(--transition-duration) ease-in;
	transition: background-color var(--transition-duration) ease-in;
}

.pagination__link:hover,
.pagination__link:focus {
	background-color: var(--brand-color);
}
:root {
	--gutter: 16px;
	--film-card-vertical-offset: 12px;
	--film-card-horizontal-offset: 8px;
	--transition-duration: 0.2s;
	--brand-color: #ffe6ab;
	--border-radius: 3px;
}

*, *::before, *::after {
	box-sizing: border-box;
}

button {
	cursor: pointer;
}

.current a {
	color: #000;
	text-decoration: none;
}

.row {
	margin-left: calc(-1 * 16px);
	margin-left: calc(-1 * var(--gutter));
	margin-right: calc(-1 * 16px);
	margin-right: calc(-1 * var(--gutter));
}

.container {
	padding-left: 16px;
	padding-left: var(--gutter);
	padding-right: 16px;
	padding-right: var(--gutter);
}
